/* App */

.app {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: large;
  color: white;
}

.app-helper {
  margin: 8px 16px;
}

#app-content {
  margin-top: 92px;
  padding: 8px;
}

/* Player */

#player-containter {
  border: lightgrey 1px solid;
  border-radius: 8px;
  padding: 16px;
  margin: 12px;
}

#player-title-style {
  margin: 8px 0px 2px;
}

#player-controls-container {
  display: flex;
  margin-top: 16px;
}

.video-container-section {
  display: flex;
  justify-content: center;
}

.video-player {
  height: 60vh;
  aspect-ratio: 16/9;
}

.roi-container {
  position: absolute;
  height: 60vh;
  aspect-ratio: 16 / 9;
  background-color: transparent;
}

.roi-canvas {
  height: 100%;
  width: 100%;
}

/* Progress bar */

.progress-controls {
  display: flex;
}

.progress-bar-container {
  width: 100%;
}

.progress-bar-wrapper {
  position: relative;
  opacity: 0.4;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.3);
}

.progress-bar-wrapper:hover {
  background-color: rgba(255, 255, 255, 0.527);
  z-index: 1;
}

.progress-bar {
  pointer-events: none;
  z-index: 0;
  top: -24px;
  position: relative;
  height: 24px;
  width: 0;
  background-color: rgb(0, 191, 0);
}

/* General */

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

  /* Camera */

.camera-wrapper {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

.camera-icon {
  margin-right: 8px;
  filter: invert(1);
}

.camera-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  border: lightgrey 1px solid;
  border-radius: 8px;
  padding: 16px;
  margin: 12px;
  width: calc(50% - 24px);
  box-sizing: border-box;
}

.camera-container:hover {
  background-color: #424956;
}


@media screen and (max-width: 480px) {
  .camera-container {
    width: 100%;
    margin: 8px;
  }
}

.camera-meta-token {
  min-width: 64px;
  margin-right: 12px;
  border: 1px white solid;
  border-radius: 8px;
  display: inline-block;
  background-color: transparent;
  color: white;
  padding: 8px 12px 8px;
}

.camera-meta-token:hover {
  background-color: #424956;
}

/* Nav */

#nav-bar-container {
  display: flex;
  align-items: center;
  padding: 16px 16px 16px 24px;
  position: fixed;
  width: 100%;
  box-shadow: 0 0 10px darkgrey;
  background-color: #282c34;
  box-sizing: border-box;
  z-index: 1;
}

#title-container {
  margin-left: 24px;
  white-space: nowrap;
  font-size: 28px;

  @media screen and (max-width: 480px) {
    display: none;
  }
}
